.signupscreen {
  max-width: 400px;
  margin: 3rem auto;
  padding: 1rem 1rem;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 10px rgba(96, 96, 96, 0.6);
  border-radius: 4px;
}

.signupscreen > * {
  margin: 1.5rem 0;
}

.signupscreen__title {
  text-align: center;
}

.signupscreen__form {
  display: flex;
  flex-direction: column;
}

.signupscreen__form > label {
  display: flex;
  margin-top: 3px;
  flex-direction: column;
}

.signupscreen__form > .signup__button {
  margin-top: 1rem;
  padding: 10px 17px;
  border: none;
  border-radius: 2px;
  background-color: #171717;
  color: #f4f4f4;
  font-size: 1rem;
}

.signupscreen__form > .signup__button:hover {
  background-color: #373737;
}

.signupscreen_footer {
  text-align: center;
}
